* {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav {
background-color: #333;
color: #fff;
}
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}
.navbars {
  background: none;
  border: none;
  color: #fff;
  transition: 0.4s;
}
.navbars:hover {
  transform: rotate(90deg);
}

.nav-icons {
  display: none;
}

.nav-items {
  height: 0;
  overflow: hidden;
  transition: 0.4s;
}

@media only screen and (min-width: 800) {
.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  
  color: #fff;
}
.navbar {
  padding: 0;
}
.navbars {
  display: none;
}
.nav-items {
  height: auto !important;
}
.nav-icons {
  display: flex;
}
}

